import React from "react";
import { DataType } from "types";
import styles from "./styles.module.scss";

export const IconsList: React.FC<{
    data: Array<DataType>;
    isFullSized?: boolean;
}> = ({ data, isFullSized }) => {
    return (
        <div className={isFullSized ? styles.fullSizedList : styles.list}>
            {data.map(({ icon, title, text }) => (
                <div key={title} className={styles.listItem}>
                    {icon}
                    <div>
                        <h4>{title}</h4>
                        <span>{text}</span>
                    </div>
                </div>
            ))}
        </div>
    );
};
